.news-single {
    .news-article {
        max-width: 60%;
        margin: 50px auto;
        text-align: center;

        .img-container-single-news {
            max-width: 600px;
            height: 200px;
            border-radius: 50%;
            margin: 50px auto;
            border-top-left-radius: 550px 550px;
            border-top-right-radius: 200% 100%;
            border-bottom-right-radius: 150% 200%;
            border-bottom-left-radius: 30% 50px;
            display: block;
            overflow: hidden;
            img{
            width: 100%;
            }
        }

        p {
            text-align: justify;
        }

    }
    .author {
        margin: 0 auto; 
        width: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 2em 3em;
        border: none;
        text-align: center;
        h3 {
            margin: 2em 0 0;
        }
        
    .img-container-author {
        max-width: 90px;
        height: 90px;
        box-shadow: 0 0 15px rgba(0,0,0,.3),inset 0 0 6px rgba(0,0,0,.3);    
        margin: 2em;
        display: block;
        overflow: hidden;
        border-radius: 50%;
        img{
            width: 100%;
            height: 100%;
            }
    }

    .txt-author{
        width: 50%;
        text-align: justify;
        h2{
            font-size: 14px;
        }
        h3{
            font-size: 13px;
            font-weight: 100;
        }
    p{
        font-size: 12px;
    }
    }
    }
}